import {
  Box,
  Button,
  CssBaseline,
  Divider,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
import Aos from "aos";
import "aos/dist/aos.css";
import { MainFooter } from "components/Footer";
import Loading from "components/Loading";
import { child, get, getDatabase, ref } from "firebase/database";
import React, { useEffect, useState } from "react";
import constants from "theme/constants";
import Project from "./Project";
import styled from "@emotion/styled";

const HEADERS = ["PROJECTS", "Load More"];

const getData = async () => {
  try {
    const dbRef = ref(getDatabase());
    const snapshot = await get(child(dbRef, `projects`));
    if (snapshot.exists()) {
      return snapshot.val();
    } else {
      console.log("No data available");
    }
  } catch (error) {
    console.log(error);
  }
  return null;
};

// create styled button
// create a styled Tab component
const StyledButton = styled(Button)(({ theme }) => ({
  borderRadius: 0,
  padding: theme.spacing(2),
  marginTop: theme.spacing(4),
  backgroundColor: theme.palette.main.purple,
  boxShadow: "none",
  transition: "all 0.2s ease-in-out",
  // add hover effect
  "&:hover": {
    boxShadow: "none",
    backgroundColor: theme.palette.main.purpleShade,
    // add hover transition
    transition: "all 0.2s ease-in-out",
  },
}));

const ProjectsPage = () => {
  const theme = useTheme();
  const INCREMENT_COUNT = 2;
  const INITIAL_COUNT = 2;

  const [filteredProjects, setFilteredProjects] = useState([]); // only holds the data filtered after dropdown selection
  const [data, setData] = useState(null); // holds the entire data list
  const [isLoading, setIsLoading] = useState(true);
  const [filter, setFilter] = React.useState("All"); // used for controlling the dropdown selection
  const [dropdownList, setDropdownList] = React.useState({}); // contains the list of dropdown options
  const [counter, setCounter] = React.useState(INITIAL_COUNT); // used for controlling the number of items shown in the page

  const { ANIMATION_STYLE } = constants;

  useEffect(() => {
    Aos.init({ duration: 1000 });
    getData()
      .then((res) => {
        const projectList = [];
        const dropdownList = {};
        dropdownList["All"] = 0;

        for (let key in res) {
          const type = res[key].type;
          if (dropdownList[type]) {
            dropdownList[type] += 1;
          } else {
            dropdownList[type] = 1;
          }

          projectList.push(res[key]);
        }
        dropdownList["All"] = projectList.length;

        setDropdownList(dropdownList);

        console.log(dropdownList);

        if (res !== null) {
          setData(projectList);
          // fetch first 2 items from projects
          setFilteredProjects(projectList);

          setIsLoading(false);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const onLoadClick = () => {
    setCounter(counter + INCREMENT_COUNT);
  };

  // filter the projects based on the dropdown selection
  const handleDropdownChange = (event) => {
    const filter = event.target.value;
    setFilter(event.target.value);

    if (filter === "All") {
      setFilteredProjects(data);
    } else {
      const filteredData = data.filter((item) => item.type === filter);
      setFilteredProjects(filteredData);
    }

    setCounter(INITIAL_COUNT);
  };

  if (data === null || isLoading) return <Loading />;

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      <CssBaseline />
      {/* content */}
      <Box
        sx={{
          flexGrow: 1,
          marginLeft: constants.CONTENT_SPACING,
          marginRight: constants.CONTENT_SPACING,
          marginTop: "1%",
        }}
      >
        <Stack spacing={1}>
          <Typography variant="h2" data-aos={ANIMATION_STYLE}>
            {HEADERS[0]}
          </Typography>
          <Divider
            data-aos={ANIMATION_STYLE}
            style={{
              marginTop: theme.spacing(2),
              marginBottom: theme.spacing(2),
            }}
          />

          {/* dropdown */}
          <FormControl
            variant="filled"
            sx={{ m: 1, minWidth: 120, maxWidth: 350, pb: 5 }}
            fullWidth={false}
            data-aos={ANIMATION_STYLE}
          >
            <InputLabel id="demo-simple-select-filled-label">
              Filter by:
            </InputLabel>
            <Select
              labelId="demo-simple-select-filled-label"
              id="demo-simple-select-filled"
              value={filter}
              onChange={handleDropdownChange}
              fullWidth={false}
            >
              {Object.keys(dropdownList).map((key, index) => (
                <MenuItem key={index} value={key}>
                  {key + " (" + dropdownList[key] + ")"}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          {filteredProjects.map((project, index) => {
            console.log("counter inside filtered:", counter);

            if (index < counter) {
              return (
                <div
                  key={index}
                  style={{
                    marginTop:
                      index === 0 ? theme.spacing(1) : theme.spacing(8),
                  }}
                >
                  <Project key={index} project={project} index={index} />
                </div>
              );
            }
            return <div key={index}></div>;
          })}
        </Stack>
      </Box>

      <Box
        sx={{
          marginTop: "10%",
          textAlign: "center",
        }}
      >
        <StyledButton
          variant="contained"
          color="primary"
          onClick={onLoadClick}
          disabled={counter >= filteredProjects.length}
        >
          {HEADERS[1]}
        </StyledButton>
      </Box>

      <Box
        sx={{ overflow: "auto", paddingTop: 20 }}
        flexDirection={"column"}
        flexGrow={1}
      />
      {/* footer */}
      <MainFooter />
    </Box>
  );
};

export default ProjectsPage;
